<template>
  <div>
    <cms-breadcrumb to="/activity/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>可进行添加平台活动</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent v-loading='loading'>
        <el-form-item label="活动名称" prop="promote.name" ref="promote_name">
          <el-input v-model="formData.promote.name"></el-input>
        </el-form-item>
         <el-form-item label="报名时间" prop="rule.signBeginTime" ref="rule.signBeginTime" :error="errorMessage['rule.signBeginTime']">
          <cms-picker disabledType='todayAfter' v-model="signTimer" style="width:380px" @change="getSignTimer" 
           value-format="yyyy-MM-dd" ></cms-picker>
        </el-form-item>
        <el-form-item label="活动时间" prop="promote.promoteBeginTime" ref="promote.promoteBeginTime" :error="errorMessage['promote.promoteBeginTime']">
          <cms-picker disabledType='todayAfter' v-model="activeTimer" style="width:380px" @change="getActiveTimer"
           value-format="yyyy-MM-dd" ref="activeTimer"></cms-picker>
        </el-form-item>
        <el-form-item label="活动说明" prop="promote.remark" ref="promote.remark">
          <el-input v-model="formData.promote.remark" type="textarea" :rows="4" maxlength="250"></el-input>
        </el-form-item>
        <el-form-item label="活动报名限制" prop="rule.signScope" ref="rule_signScope">
          <el-radio-group v-model="formData.rule.signScope">
            <el-radio :label="1">全部商品</el-radio>
            <el-radio :label="2">自营商品</el-radio>
            <el-radio :label="3">部分类目</el-radio>
          </el-radio-group>
        </el-form-item>
        <!-- 选择类目 -->
        <el-form-item label="选择类目" v-show="formData.rule.signScope===3">
          <category-cascader style="width:380px;" v-model="formData.signLimitCtgIds"></category-cascader>
        </el-form-item>
        <el-form-item label="活动支持优惠形式" prop="promote.promoteType" ref="promote.promoteType">
          <el-radio-group v-model="formData.promote.promoteType">
            <el-radio :label="4">优惠券</el-radio>
            <el-radio :label="3">满减</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="选择优惠券" v-show="formData.promote.promoteType==4">
          <cms-coupon v-model="formData.couponIds" v-if="status" @get="getCoupon"
           :type=1 :hasPromote="false"></cms-coupon>
        </el-form-item>
        <el-form-item label='满减信息' v-show="formData.promote.promoteType==3">
          <ul>
            <li v-for="(item,index) in formData.discounts" :key="index" style="margin-bottom:10px;">
              <el-input style="width:160px;margin-right:5px" v-model="item.conOrderAmount"  type="number"
               oninput="if(value.length>9)value=value.slice(0,9)">
                <template slot="prepend">满</template>
              </el-input>
              <el-input style="width:160px;" v-model="item.discountAmount" type="number" 
               oninput="if(value.length>9)value=value.slice(0,9)">
                <template slot="prepend">减</template>
              </el-input>
              <span class="del-span" @click="discountDelete(index)" v-if="index!=0">删除</span>
            </li>
            <div class="discounts__add" :style="{color:color}" @click="discountAdd()">
              <span class="iconfont icon-tianjia"></span>添加</div>
          </ul>
        </el-form-item>
        <el-form-item label="自营店铺单品收费" prop="rule.oneFeeThird" ref="rule.oneFeeThird">
          <el-input v-model="formData.rule.oneFeeThird" maxlength="6">
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="非自营店铺单品收费" prop="rule.oneFeeSelf" ref="rule.oneFeeSelf">
          <el-input v-model="formData.rule.oneFeeSelf" maxlength="6">
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <h3 class="cms-segmentation">商品报名限制</h3>
        <el-form-item label="商品报名限制">
          <el-select v-model="productLimt" multiple size="mini" @change="changeProductLimt">
            <el-option :value="0" label="无限制"></el-option>
            <el-option :value="1" label="最新30天成交价"></el-option>
            <el-option :value="2" label="库存"></el-option>
            <el-option :value="3" label="商品评分"></el-option>
            <el-option :value="4" label="退款率"></el-option>
            <el-option :value="5" label="最新30天销量"></el-option>
          </el-select>
        </el-form-item>
        <!-- 成交价 -->
        <el-form-item label="最新30天成交价>=" prop="rule.minPrice" ref="rule.minPrice" v-if="productLimt.indexOf(1)>-1">
          <el-input v-model="formData.rule.minPrice" maxlength="6">
            <template slot="append">元</template>
          </el-input>
        </el-form-item>
        <el-form-item label="库存>=" prop="rule.minStock" ref="rule.minStock" v-if="productLimt.indexOf(2)>-1">
          <el-input v-model="formData.rule.minStock" maxlength="6">
            <template slot="append">件</template>
          </el-input>
        </el-form-item>
        <el-form-item label="商品评分>=" prop="rule.minScore" ref="rule.minScore" v-if="productLimt.indexOf(3)>-1">
          <el-input v-model="formData.rule.minScore" maxlength="4">
            <template slot="append">分</template>
          </el-input>
        </el-form-item>
        <el-form-item label="退款率<=" prop="rule.maxRefundRate" ref="rule.maxRefundRate" v-if="productLimt.indexOf(4)>-1">
          <el-input v-model="formData.rule.maxRefundRate" maxlength="4">
            <template slot="append">%</template>
          </el-input>
        </el-form-item>
        <el-form-item label="最新30天销量>=" prop="rule.minSale" ref="rule.minSale" v-if="productLimt.indexOf(5)>-1">
          <el-input v-model.number="formData.rule.minSale" maxlength="6">
            <template slot="append">件</template>
          </el-input>
        </el-form-item>
        <el-form-item label="活动检索标识" prop="promote.product_flag_icon">
          <cms-picture-zone v-model="formData.promote.product_flag_icon" v-if="status"></cms-picture-zone>
        </el-form-item>
        <el-form-item label="是否上线" prop="promote.enable" ref="promote.enable">
          <cms-switch v-model="formData.promote.enable"></cms-switch>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type='rest' @click="reset">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { activitySave } from '@/api/marketing/index.js'
import categoryCascader from '@/components/cms/categoryCascader'
import cmsCoupon from './../../../components/operation/cmsCoupon'
var defaultData = {
  promote: {
    name: '',
    promoteBeginTime: '',
    promoteEndTime: '',
    remark: '',
    enable: true,
    promoteType: 4,
    product_flag_icon: ''
  },
  rule: {
    signBeginTime: '',
    signEndTime: '',
    signScope: 1,
    oneFeeThird: 1,
    oneFeeSelf: 0.5,
    maxRefundRate: 0.1,
    minPrice: 50,
    minSale: 20,
    minScore: 4.5,
    minStock: 100
  },
  discounts: [
    {
      conOrderAmount: 0,
      discountAmount: 0
    }
  ],
  signLimitCtgIds: [],
  couponIds: []
}

export default {
  components: { categoryCascader, cmsCoupon },
  mixins: [form],
  data() {
    return {
      productLimt: [], // 商品报名限制
      activeTimer: [],
      signTimer: [],
      formData: Object.assign({}, defaultData),
      rules: {
        'promote.name': [rules.required('请填写活动名称'), rules.len(1, 15)],
        'promote.promoteBeginTime': [rules.required('请选择活动开始时间', 'change')],
        'rule.signBeginTime': [rules.required('请选择活动报名时间', 'change')],
        'rule.oneFeeThird': [rules.required(), rules.float(), rules.decimal()],
        'rule.oneFeeSelf': [rules.required(), rules.float(), rules.decimal()],
        'rule.minPrice': [rules.float(), rules.decimal()],
        'rule.minStock': [rules.number()],
        'rule.minScore': [rules.float()],
        'rule.maxRefundRate': [rules.float()],
        'rule.minSale': [rules.number()]
      }
    }
  },
  computed: {
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    changeProductLimt(value) { // 商品报名限制下列表
      if (value.indexOf(0) > -1 && value.length > 1) {
        this.productLimt = []
      }
    },
    getCoupon(items) { // 得到优惠券相关
      this.formData.couponIds = items.map(v => v.id)
    },
    changeType(value) {
      this.formData.coupon.expirateType = value
    },
    getActiveTimer(value) { // 获取活动时间
      this.formData.promote.promoteBeginTime = value[0]
      this.formData.promote.promoteEndTime = value[1]
    },
    getSignTimer(value) { // 获取报名时间
      this.formData.rule.signBeginTime = value[0]
      this.formData.rule.signEndTime = value[1]
      this.$refs.activeTimer.setFilterTime(value[1])
    },
    discountAdd() { // 添加减满信息
      this.formData.discounts.push({
        conOrderAmount: 0,
        discountAmount: 0
      })
    },
    discountDelete(index) { // 删除减满信息
      this.formData.discounts.splice(index, 1)
    },
    save(state) {
      const params = this.$cms.deepCopy(this.formData)
      let flag = true // 此处用来过滤减满信息的脏数据
      if (params.promote.promoteType === 3) {
        params.couponIds = []
        // 提交前要处理不同的优惠类型，避免脏数据
        const reg = /^(([0-9]*)|(([0]\.\d{1,2}|[0-9]*\.\d{1,2})))$/
        params.discounts.forEach(v => {
          if (v.conOrderAmount === '' || v.discountAmount === '') {
            flag = false
            this.$errorMessage('满减信息不能为空')
          } else if (!reg.test(v.conOrderAmount) || !reg.test(v.discountAmount)) {
            flag = false
            this.$errorMessage('满减信息格式不正确')
          } else if (parseFloat(v.conOrderAmount) < parseFloat(v.discountAmount)) {
            flag = false
            this.$errorMessage('满减额度必须大于消费额度')
          } else { // 满足添加之后再转化一下
            v.conOrderAmount = parseFloat(v.conOrderAmount)
            v.discountAmount = parseFloat(v.discountAmount)
          }
        })
      } else {
        params.discounts = []
      }
      // 处理掉商品限制数据
      if (this.productLimt.indexOf(1) === -1) { // 若限制的数组里没有对应的value值(则当前项置为0)
        params.rule.minPrice = 0
      }
      if (this.productLimt.indexOf(2) === -1) {
        params.rule.minStock = 0
      }
      if (this.productLimt.indexOf(3) === -1) {
        params.rule.minScore = 0
      }
      if (this.productLimt.indexOf(4) === -1) {
        params.rule.maxRefundRate = 0
      }
      if (this.productLimt.indexOf(5) === -1) {
        params.rule.minSale = 0
      } else {
        params.rule.minSale = parseInt(params.rule.minSale)
      }
      if (flag) { // 当减满信息符合规范
        const signTimerEnd = new Date(this.signTimer[1]).getTime()// 报名结束时间
        const activeTimerStart = new Date(this.activeTimer[0]).getTime()// 活动开始时间

        if (activeTimerStart > signTimerEnd) {
          this.saveForm(activitySave, params, state, '/activity/list', 'list').then(
            res => {
              // 清空时间和报名限制
              this.activeTimer = []
              this.signTimer = []
              this.productLimt = ''
            }
          )
        } else {
          this.errorPosition('promote.promoteBeginTime', {
            'promote.promoteBeginTime': '活动开始时间必须在报名阶段结束之后'
          })
        }
      }
    },
    reset() { // 重置
      window.clearInterval(this.timer)
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      // 清空时间和报名限制\
      this.formData = this.$cms.deepCopy(defaultData)
      this.activeTimer = []
      this.signTimer = []
      this.productLimt = []
    }
  },
  created() {
    this.formData = this.$cms.deepCopy(defaultData)
  }
}
</script>

<style scoped>
.discounts__add {
  cursor: pointer;
}
.discounts__add .iconfont {
  position: relative;
  top: 1px;
  margin-right: 3px;
}
</style>



