
<template>
  <div>
    <el-button @click="initData" size="mini" type="primary">选择优惠券</el-button>
    <el-dialog title='优惠券选择' :visible="dialogVisible" width="70%" :before-close="close">
      <div class="cms-dialog__body">
      <section class="cms-list-query">
         <div class="query-group">
        <label>优惠券名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
          <div class="query-group">
            <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
          </div>
        </section>
        <el-table  v-loading='loading' element-loading-text="正在加载..." :data="tableData" style="cursor: pointer;" 
         ref='dataTable' @selection-change='getCheckItems' @row-click='checkRows' >
          <!-- 多选 -->
          <el-table-column width="60" type="selection" v-if="multiple">
          </el-table-column>
          <!-- 单选 -->
          <el-table-column width="60" align="center" v-else>
            <div slot-scope="scope">
              <el-radio :label="scope.$index" v-model="selectIndex" :disabled="canSelecteAll" >&nbsp;</el-radio>
            </div>
          </el-table-column>
          <el-table-column prop="id" label="id" width="60"></el-table-column>
          <el-table-column prop="name" label="优惠券名称" min-width="200" show-overflow-tooltip class-name="text-overflow"></el-table-column>
          <el-table-column prop="couponType" label="类型" align="center" width="120">
            <div slot-scope="scope">
              <span v-if="scope.row.couponType==1" class="info">活动优惠券</span>
              <span v-if="scope.row.couponType==2" class="success">普通优惠券</span>
            </div>
          </el-table-column>
          <el-table-column prop="discountAmount" label="面值"></el-table-column>
          <el-table-column prop="discountAmount" label="使用条件" min-width="160">
            <div slot-scope="scope">
              <span>满{{scope.row.conOrderAmount}}减{{scope.row.discountAmount}}</span>
            </div>
          </el-table-column>
          <el-table-column prop="discountAmount" label="使用期限" min-width="180">
            <div slot-scope="scope">
              <span v-if="scope.row.expirateType==1">{{$moment(scope.row.useBeginTime,'YYYY-MM-DD')}} 至 
                {{$moment(scope.row.useEndTime,'YYYY-MM-DD')}}
              </span>
              <span v-if="scope.row.expirateType==2">派发后{{scope.row.useDay}}天</span>
            </div>
          </el-table-column>
        </el-table>
        <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
       <el-button type="primary" size="mini" @click="determine">确 定</el-button>
        <el-button type='info' size="mini" @click="close">取 消</el-button>
      </span>
    </el-dialog>
    <ul class="coupon__items">
      <li v-for="(item,index) in showItems" :key="item.id" class="coupon__item">
        <span class="coupon__name text-overflow">{{item.name}}</span>
        <span class="coupon__del" @click="deleteCoupon(index)">删除</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { couponPage } from '@/api/marketing/index.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsCoupon',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    labelName: {
      type: String,
      default: 'name'
    },
    type: {// 优惠券的类型
      type: Number,
      default: ''
    },
    hasPromote: {
      type: Boolean,
      default: ''
    },
    data: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  watch: {
    data: {
      handler() {
        this.checkItems = this.$cms.deepCopy(this.data)
        this.showItems = this.$cms.deepCopy(this.data)
      },
      immediate: true,
      deep: true
    }
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        name: '',
        discountAmount: '',
        beginTime: '',
        endTime: '',
        couponType: '',
        hasPromote: '',
        page: 1,
        size: 10
      },
      showItems: [] // 组件内显示的数组
    }
  },
  methods: {
    initData() {
      this.dialogVisible = true
      this.queryParams.couponType = this.type// 优惠券的类型
      this.queryParams.hasPromote = this.hasPromote// 优惠券的转态是否被占用
      this.getTableData(couponPage, this.queryParams)
    },
    checkRows(row) {
      // 单击行选中
      if (this.multiple) {
        // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else {
        // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.checkItems)
      if (this.multiple) {
        // 多选时
        this.$emit('input', this.checkItems.map(v => v.id))
      } else {
        // 单选时
        this.$emit(
          'input',
          parseInt(this.checkItems.map(v => v.id).toString())
        )
      }
      this.showItems = this.$cms.deepCopy(this.checkItems)
      this.close()
    },
    deleteCoupon(index) {
      this.showItems.splice(index, 1)
      this.checkItems.splice(index, 1)
      this.$emit('get', this.checkItems)
      if (this.multiple) {
        // 多选时
        this.$emit('input', this.checkItems.map(v => v.id))
      } else {
        // 单选时
        this.$emit(
          'input',
          parseInt(this.checkItems.map(v => v.id).toString())
        )
      }
    },
    close(done) {
      this.checkItems = []
      this.selectIndex = ''
      this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
.coupon{
  &__item{
    height: 28px;
    line-height: 28px;
  }
 &__name{
   display: inline-block;
   font-size: 12px;
   color: #444;
   width: 160px;
 }
 &__del{
    display: inline-block;
    vertical-align: top;
   color: #999;
   cursor: pointer;
   &:hover{
     color: #fe5331;
   }
 }
}
</style>



